@import "reset.css";
@import "formStyle";

$grey-color: #514840;
$brown-color: #8D5C34;
$white-color: white;
$border-radius: 8px;
$base-font-size: 16px;
$small-font-size: .8rem;
$medium-font-size: 1rem;
$large-font-size: 1.5rem;
$small-header-font-size: 1rem;
$medium-header-font-size: 1.3rem;
$large-header-font-size: 2.5rem;

@mixin flex-box($direction: column, $justify-content: space-between, $align-items: center, $gap: 2vw, $flex-wrap: nowrap){
  display: flex;
  flex-direction: $direction;
  justify-content: $justify-content;
  align-items: $align-items;
  gap: $gap;
  flex-wrap: $flex-wrap;
}

@mixin wrapper-margin($margin-v: auto, $margin-h: 5vw){
  margin: $margin-v $margin-h;
}

html {
  font-size: $base-font-size;
  overflow:  hidden;
}

h1 {
  font-size: $large-header-font-size; 
  font-weight: bolder;
}

h2 {
  font-size: $medium-header-font-size; 
  font-weight: bold;
}

button {
  background-color: $brown-color;
  color: $white-color;
  padding: 14px 20px;
  // margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  border-radius: $border-radius;

  &:hover{
    opacity: 0.8;
  }
}

img {
  height: 100%;
  width: 100%;
}

.img-icon{
  width: max(1.5vw, 20px);
  height: max(1.5vw, 20px); 
}

.img-round-border{
  border-radius: $border-radius;
}

.smallText{
  font-size: $small-font-size;
  color: $grey-color;
}

.text-shadow{
  span{
    background-color: $grey-color;
    opacity: 0.8; 
    color: $white-color;
    border-radius: $border-radius;
    padding: 0 10px;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.wrapper-first-page{
  background: url(../files/images/CyprusBackgroundHorizontal.jpg) no-repeat;
  @media (max-width: 500px) {
    background: url(../files/images/CyprusBackgroundVertical.jpg) no-repeat;
    background-size: cover;     
  }
  background-size: cover;   
  height: calc(100vh - 50px);

  .span{
    line-height: 1.2;
  }
}

.main-info{
  text-align: center;
  color: $white-color;

  h1{
    padding-top: 20vh;
  }

  div{
    @include flex-box(row, center, center, 2vw, wrap);
    margin: 5vh 15vw;
    button{
      max-width: 300px;
    }
  }
}

.wrapper-complexes{
  display: grid;
  grid-gap: 10px;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, 350px);
  @include wrapper-margin();

  div{
    margin: 5px auto;
  }

  .complex-box__description div p{
    margin-left: 1vw;
  }
}

.wrapper-complex{
    @include flex-box(column, center, center, 2vw);
    @include wrapper-margin();

    .apartment-box__details{
      @include flex-box(row, left, top, 2vw, wrap);
      box-shadow: 1px 5px 10px 0.2px $grey-color;
      border-radius: $border-radius;
      padding: 1vw;

      div span{
        border: solid;
        border-radius: $border-radius;
        padding: 2px 5px;
      }

      &:nth-child(1){
        flex-basis: 20%;
      }
      &:nth-child(2){
        flex-basis: 20%;
      }
      &:nth-child(3){
        flex-basis: 20%;
      }
      &:nth-child(4){
        flex-basis: 40%;
      }
      .apartment-box__propertyOptions span{
        margin: auto .5vw;
        padding: 2px 5px;
      }
      .apartment-box__like{
        width: 70px;
        padding: 5px 5px;
      }
    }
}




















.header {
  flex: 0 0;
  margin: 1vh 2vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20vw;
}

.header-left{
  @include flex-box(row);
}

.header-right{
  @include flex-box(row, right, center, 2vw, wrap);
}

.contacts{
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2vw;
  // font-size: 16px;
}

.contacts__phone{
  @media (max-width: 768px) {
    font-size:0;
  }
  font-size: 16px;
}

.navigation{
  @include flex-box(row, right, center, 1vw, wrap);
  font-size: 16px;
}

.log-in{
  // margin-left: 2vw;
}

.main {
    flex: auto;
}





.additional-info{
  display: flex;
  flex-wrap: wrap;
}

.additional-info{
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2vw;
}



footer {
    height: 40px;
}

.logo{
  width: max(6vw, 50px);
  height: max(3vw, 25px);;
  object-fit: cover;
}